// TODO Need docs

let __lockFlag = false;
const lock = () => {__lockFlag = true};
const unlock = () => {__lockFlag = false};
const isLock = () => __lockFlag;

const noop = () => {};
const now = () => Math.round(performance.now());

/**
 * 指定位置までスクロールアニメーションを実行
 *
 * @param   targetY     {number|string} - 指定位置(px)もしくは指定要素
 * @param   [callback]  {function}      - 処理完了時のコールバック
 * @return  {boolean}   - アニメーションを実行完了したかどうか
 */
const scrollTo = (targetY, callback = noop) => {
  if (typeof targetY === 'undefined') throw new Error('This parameter is required.');
  if (!(typeof targetY === 'number' || typeof targetY === 'string')) throw new TypeError('This parameter is number or string.');

  if (typeof targetY === 'string') {
    const targetElement = document.querySelector(targetY);
    if (!targetElement) return callback(false);
    const targetTop = targetElement.getBoundingClientRect().top;
    targetY = window.scrollY + targetTop;
  }

  if (targetY < 0) return callback(false);
  if (isLock()) return callback(false);

  lock();

  // NOTE: iOS と PC でスクロール位置の計算が異なるため。またIE対策。
  const scrollingElement = document.scrollingElement || document.documentElement;

  // 上方向かどうか
  const upward = scrollingElement.scrollTop > targetY;

  // 対象の要素までのスクロール距離
  const distance = () => Math.abs(scrollingElement.scrollTop - targetY);

  // floorでは1.0未満の時に、roundでは0.5以下の場合は0になってしまいstep:0で動作しなくなるため必ず ceil
  let step = Math.ceil(distance() / 50);

  let interval = 10;

  let prevScrollTop =  scrollingElement.scrollTop;

  let isPause = false;
  let pauseTime;

  const done = (doneStatus = true) => {
    unlock();
    return callback(doneStatus);
  };

  const scrolling = () => {
    // 対象要素までの距離が近づいたらその位置まで強制的に移動させる
    if (distance() <= 1) {
      scrollingElement.scrollTop = targetY;
      return done();
    }

    // 対象要素までの距離がステップ数を下回ったら徐々にスクロールさせる
    if (distance() < step) {
      // step = 2;
      step = Math.ceil(distance() / 2);
      interval = 1;
    }

    if (upward) {
      // 上方向にスクロールする場合の処理
      if (scrollingElement.scrollTop <= targetY) {
        return done();
      }
      scrollingElement.scrollTop -= step;
    } else {
      // 下方向にスクロールする場合の処理
      if (scrollingElement.scrollTop >= targetY) {
        return done();
      }
      scrollingElement.scrollTop += step;
    }

    // スクロール位置が止まったままになっていないか確認
    if (prevScrollTop === scrollingElement.scrollTop) {
      if (!isPause) {
        pauseTime = now();
      }
      isPause = true;
    } else {
      isPause = false;
    }

    // スクロール位置が変わらないまま1000ミリ秒経過したら処理を抜ける（対象要素が最下部のときやフリーズ時の対策）
    if (now() - pauseTime > 1000) {
      return done();
    }

    prevScrollTop = scrollingElement.scrollTop;

    setTimeout(scrolling, interval);
  };

  scrolling();
};

export default scrollTo;
